<template>

  <g>


    <a @click="nodeSelect()">

      <rect :x="rBox.l" :y="rBox.t" rx="5" ry="5" :width="rBox.w"
            :height="rBox.h" style="fill:red;stroke:black;
                      stroke-width:2;opacity:0.5"/>

      <text :x="tBox.l " :y="tBox.t" font-size="12">{{nodeName }}</text>
    </a>

      <path :d="sPath" stroke="black" fill="transparent"/>
    <relation-show v-if="ShowNode"  v-for="(childNodeId, index) in childList"
                     :key="index"
                     :node-id="childNodeId"
                     :current-level="NextLevel"
                     :parent-id="nodeId"
                     :parent-rect = "rBox"
                     :node-no="index"
                     :node-type ="nodeType">
  </relation-show>


  </g>

</template>

<script>

  import store from '../../store/index'

  export default {

    name: "relationShow",
    components: {store},
    props:
      {
        nodeNo: null,
        nodeId:null,
        parentId:null,
        currentLevel:null,

        parentRect:null,
        nodeType:null
      },
    created()
    {},

    data() {
      return {

        rBox:{l: 0, t: 0, w: 0, h: 0,n:0,c:0},
        tBox:{l: 0, t: 0, w: 0, h: 0},
        //showNode:false,
        pWidth :40,
        sPath: "M30 300 C 50 310, 40 10, 80 10",
        nodeInfo:{},
        childList: [],
        nodeName:"",

      }

    },
    created()
    {

      this.getNodeInfo(this.nodeId)

    },
    computed:
      {

        ShowNode:function()
        {
            console.debug("ShowNode wwwwwwwwwwwwwwwwww");
            let a = store.state.nodeChangeCount ;

            let b = store.getters["icbc/getShowFlag"](this.currentLevel,this.nodeNo);
            console.debug(this.currentLevel,this.nodeNo,b)
            return b;

        },
        NextLevel: function()
        {
          console.debug("currentLelel:" + this.currentLevel)
          return this.currentLevel+1;
        }
      },
    methods:
      {

        setBox()
        {

          let count = this.childList.length;
          let len = this.nodeName.length;
          //if(len>15) len = 15;
          let pChildCount = this.parentRect.c
          let pParentNo = this.parentRect.n +1
          let topMargin = 40
          let splitH = 10;
          let hight = 25
          this.rBox.n = this.nodeNo
          this.rBox.c = count
          this.rBox.w = 15 *14
          this.rBox.h = 25
         if(pParentNo>0.5*pChildCount)
            topMargin = (pParentNo - 0.5*pChildCount)*(this.rBox.h + splitH) + topMargin

          this.rBox.l = this.parentRect.l+ this.parentRect.w + this.pWidth;

          this.rBox.t = this.nodeNo* (this.rBox.h +splitH)  +topMargin;
          if(pChildCount==1) this.rBox.t = this.parentRect.t;
          this.tBox.l = this.rBox.l +6;
          this.tBox.t = this.rBox.t +16;
          this.tBox.w = this.rBox.w *0.8;
          this.tBox.h = this.rBox.h *0.8

          let pBox = {x1: 0, y1: 0, x2: 40, y2: 40};
          pBox.x1 = this.parentRect.l + this.parentRect.w
          pBox.y1 = this.parentRect.t + 0.5* this.parentRect.h
          pBox.x2 = this.rBox.l
          pBox.y2 = this.rBox.t +  0.5* this.rBox.h

          this.sPath = "M" + pBox.x1 + " " + pBox.y1
                    + " C " + (pBox.x1 +0.5* this.pWidth  ) +" " + (pBox.y1 + 0.5* (pBox.y2 - pBox.y1) )
                    +"," + (pBox.x1 + 0.5* (pBox.x2-pBox.x1)) + " " + pBox.y2
                    +"," + pBox.x2  + " " +pBox.y2;



           // M30 300 C 50 310, 40 10, 80 10

        },

        nodeSelect()
        {
          console.debug("nodeSelect");
          store.state.nodeChangeCount++;
          store.commit('icbc/setSelectNode', {level:this.currentLevel, no: this.nodeNo})
        },
        getNodeInfo(nodeId)
        {

          this.nodeInfo = store.getters["icbc/getNode"](nodeId)
          if(this.nodeInfo==null) return;
          this.nodeName = this.nodeInfo.name;
          this.nodeName = this.nodeName.substring(0,14)
          this.childList = store.getters["icbc/getChild"](nodeId,"source")
        //  this.childList = store.getters["icbc/getChild"](nodeId,this.nodeType)

          if(this.childList==null||this.childList.length==0) this.showNode = false;
          else this.showNode = true;
          this.setBox()
        },

      },
    watch:
      {
        nodeId(newVal, oldVal) {
          getNodeInfo(newVal)
        },


        },

  }
</script>

<style scoped>

</style>



