<template>

  <el-container>
    <el-header>
      <el-row :gutter="20">

        <el-col :span="6">
          <wedit v-model="key0" ref="keyfld" input-hint="请输入key"></wedit>
        </el-col>
        <el-col :span="6">
          <wselect v-model="keyType" input-hint="请选择key类型" list-name="icbcKeyType"></wselect>
        </el-col>

        <el-col :span="6">
          <wselect v-model="models" input-hint="请选择model类型" list-name="icbcRelationModel"></wselect>
        </el-col>


        <el-col :span="6">


          <wbutton  task-name="icbcRelation" :task-para="getRelationTaskPara" @taskFinish="taskFinish" >关联查询</wbutton>

        </el-col>

      </el-row>

    </el-header>

    <el-container>
      <div style="height:600px;width:100%;overflow:auto;">

        <el-scrollbar>
          <svg xmlns="http://www.w3.org/2000/svg" :view-box.camel="viewbox">
            <g>
              <a>
                <rect :x="rBox.l" :y="rBox.t" rx="5" ry="5" :width="rBox.w"
                      :height="rBox.h" style="fill:red;stroke:black;
                      stroke-width:2;opacity:0.5"/>
                <text :x="tBox.l" :y="tBox.t" font-size="12">{{nodeInfo.name + childList.length}}</text>
              </a>
              <relation-show v-for="(childNodeId, index) in childList"
                             :key="index"
                             :node-id="childNodeId"
                             :current-level="NextLevel"
                             :parent-id="nodeInfo.id"
                             :parent-rect = "rBox"
                             :node-no="index"
                             :node-type ="childNodeType">
              </relation-show>

            </g>
          </svg>


        </el-scrollbar>
      </div>
    </el-container>

  </el-container>


</template>

<script>
  import wselect from "../wcontrol/wselect";
  import mselect from "../wcontrol/mselect";
  import wedit from "../wcontrol/wedit";
  import wbutton from "../wcontrol/wbutton";
  import relationShow from "./relationShow";
  import store from '../../store/index'

  export default {
    name: "relation",
    components: {wselect,mselect, wedit, wbutton, relationShow, store},
    data() {
      return {
        svgBox: {l: 10, t: 10, w: 1200, h: 1800},
        rBox: {l: 0, t: 0, w: 0, h: 0,n:0,c:0},
        tBox: {l: 0, t: 0, w: 0, h: 0},
        currentLevel: 0,
        keyType: "",
        key0: "",
        oldKey: "",
        models: "",
        nodeInfo: {},
        relationdata: [{name: "小米", box: {l: 100, t: 100, w: 60, h: 50}}],
        childList: [],
        childNodeType: "target", //"target"  source

      }
    },
    created() {

    },
    computed:
      {
        getRelationTaskPara() {
          console.debug("relationTaskPara")
          this.keyType = "32"
          this.key0 = "小米科技有限责任公司"
          this.models = "FAMILYTREE"
          let para = {taskName: "icbcRelation", key: this.key0, model: this.models, keyType: this.keyType};
          console.debug(para)
          return para;
        },
        getIcbcRelation() {
          console.debug(this.relationdata);

        },
        viewbox() {
          return "" + this.svgBox.l + " " + this.svgBox.t + " " + this.svgBox.w + " " + this.svgBox.h
        },
        NextLevel() {
          console.debug("currentLelel:" + this.currentLevel)
          return this.currentLevel + 1;
        }

      },
    methods: {
      taskFinish(data)
      {
        this.relationdata = data;
      },
      setBox() {
        //let totalLevel = store.getters("icbc/getTotalLevel")
        let topMargin = 40
        let hight = 20
        let splitH = 20;
        let count = this.childList.length;
        let no = 0.5*(count)-1;
        let len = this.key0.length;
        this.rBox.l = 25;
        this.rBox.w = len * 15
        this.rBox.h = hight +10
        this.rBox.t = no * (hight + splitH)  + topMargin;
        this.rBox.n = no;
        this.rBox.c = count;


        this.tBox.l = this.rBox.l + 10;
        this.tBox.t = this.rBox.t + 18;
        this.tBox.w = this.rBox.w * 0.8;
        this.tBox.h = this.rBox.h * 0.8
      }
    },
    watch:
      {
        relationdata(newVal, oldVal) {
          if (this.oldKey == this.key0) return;
          this.oldKey = this.key0;
          console.debug(newVal);
          if (newVal != null) {
            let data = newVal.items[0].info[0].TDATA[0];
            console.debug("333333333")
            data["key"] = this.oldKey;
            store.commit('icbc/setRelationCommit', data)
            console.debug(store)
            this.nodeInfo = store.getters["icbc/getNode"](this.key0)
            this.childList = store.getters["icbc/getChild"](this.nodeInfo.id, this.childNodeType)

            // $store.getters["wins/getAppList"]
            this.setBox()
            console.debug(this.childList);
          }

        },

      },


  }
</script>

<style scoped>
  svg {
    width: 100%;
    height: 100%;
  }

</style>



